import { formatTime } from '@/static/js/utils';

export default {
  props: {
    header: {
      type: Boolean,
      value: false
    },
    statusLock: {
      type: Boolean,
      value: false
    },
    data: {
      type: Array,
    }
  },
  methods: {
    lastedUpdateTime(value) {
      return formatTime(value)
    },
    handleUserCheckProduct(item, index) {
      if (item.hasExchanged) {
        // TODO Navigator URL
        return
      }

      this.$emit('userExchangeProduct', index);
    },
  },
  computed: {

  },
};
